
.cover{
  height: 800px;
  width: 100%;
  max-width: 1300px;
  margin-top: 0;
}


@keyframes clip1{
  0%{clip-path: polygon(0% -50%, 50% -50%, -50% 50%, -100% 50% );}
  100%{clip-path: polygon(150% 50%, 200% 50%, 100% 150%, 50% 150%);}
}
@keyframes clipa{
  0%{clip-path: polygon(-5% -55%, 55% -45%, -45% 55%, -95% 55% );}
  100%{clip-path: polygon(150% 50%, 200% 50%, 100% 150%, 50% 150%);}
}
@keyframes clip2{
  0%{clip-path: polygon(50% -50%, 100% -50%, 200% 50%, 150% 50%);}
  100%{clip-path: polygon(-100% 50%, -50% 50%, 50% 150%, 0% 150%);}
}
@keyframes clipb{
  0%{clip-path: polygon(40% -60%, 100% -50%, 190% 40%, 150% 50%);}
  100%{clip-path: polygon(-100% 50%, -50% 50%, 50% 150%, 0% 150%);}
}
@keyframes clip3{
  0%{clip-path: polygon(-100% -150%, -50% -150%, -150% -50%, -200% -50%);}
  100%{clip-path: polygon(150% 50%, 200% 50%, 100% 150%, 50% 150%);}
}
@keyframes clip4{
  0%{clip-path: polygon(150% -150%, 200% -150%, 300% -50%, 250% -50%);}
  100%{clip-path: polygon(-100% 50%, -50% 50%, 50% 150%, 0% 150%);}
}

.clip{
  background-image: url('https://img1.goodfon.com/wallpaper/nbig/b/7f/oliviya-palermo-olivia-palermo-6110.jpg');
  /* background:linear-gradient(to left, #10412a, #06662b); */
  height: 850px;
  width: 100%;
  max-width: 900px;
  margin-left: 20%;
  background-size: contain;
  background-position:center center;
  background-repeat: no-repeat;
 position: absolute;
}

#clip1 { animation:clip1 4s linear infinite; }
#clip4 {
  transform: scale(1.13,1.13);
  animation:clip4 6s linear infinite;  
}
#clipa {
  transform: scale(1.1,1.1);
  animation:clipa 9s linear infinite;  
}
#clip2 {
  transform: scale(1.12,1.12); 
  animation:clip2 5s linear infinite;  
}
#clipb {
  transform: scale(1.14,1.14);
  animation:clipb 4s linear infinite;  
}
#clip3 {
  transform: scale(1.15,1.15);
  animation:clip3 7s linear infinite;  
}

@media only screen and (min-width : 468px) and (max-width : 1024px){
  .cover{
    height: 800px;
    width: 100%;
    max-width: 1300px;
    margin: 0 !important;
    padding: 0 !important;
  }

  .clip{
    background-image: url('https://img1.goodfon.com/wallpaper/nbig/b/7f/oliviya-palermo-olivia-palermo-6110.jpg');
    /* background:linear-gradient(to left, #10412a, #06662b); */
    height: 850px;
    width: 100%;
    max-width: 1000px;
    margin: 0 !important;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    display: block;
  }
}

