.responsive2{
  object-fit: cover;
  height: auto;
  width: 100%;
}

.widget img{
  width: 100%; 
  transition: 0.5s all ease-in-out; 
}

.widget img:hover{
  transform: scale(.9); 
}



.widget { 
  position: relative;
 
}

.widget .center{
  position: absolute;
  z-index: 999;
  margin: 0 auto;
  left:0;
  right: 0;
  top: 50%; 
  text-align: center;
  width: 60%; 
  font-size: 80px;
  font-family: Dancing Script;
}

 .widget .center1 { 
  position: absolute;
  z-index: 999;
  margin: 0 auto;
  left:0;
  right: 4;
  top: 20%; 
  text-align: center;
  width: 60%; 
  font-size: 80px;
  font-family: Dancing Script;
}

