

.nav{
    border: none;
    font-size: 20px;
    
  }
  
  .navbar-brand{
    font-size: 40px;
    font-weight: bold;
    font-family: 'Zilla Slab', serif;
   
  }

  .dropdown-item{
      font-size: 20px;
      font-family: 'Zilla Slab', serif;
     
  }


  .dropdown-toggle{
    font-size: 20px;
    font-family: 'Zilla Slab', serif;
   
  }

  .navRight{
    font-size: 20px;
    font-family: 'Zilla Slab', serif;
    display: inline;
  }
  
  /* media query starts .............................................................................*/
  
  @media only screen and (min-width : 768px) and (max-width : 1024px) and (orientation : landscape) {
    .navbar-header {
          float: none;
      }
      .navbar-left,.navbar-right {
          float: none !important;
      }
      .navbar-toggle {
          display: block;
    
      }
      .navbar-collapse {
          border-top: 1px solid transparent;
          box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
      }
      .navbar-fixed-top {
          top: 0;
          border-width: 0 0 1px;
      }
      .navbar-collapse.collapse {
          display: none!important;
      }
      .navbar-nav {
          float: none!important;
          margin-top: 7.5px;
      }
      .navbar-nav>li {
          float: none;
      }
      .navbar-nav>li>a {
          padding-top: 10px;
          padding-bottom: 10px;
      }
      .collapse.in{
            display:block !important;
        border:1px solid black !important;
       overflow:scroll;
      }
  }
  @media (max-width: 992px) {
      .navbar-header {
          float: none;
      }
      .navbar-left,.navbar-right {
          float: none !important;
      }
      .navbar-toggle {
          display: block;
      }
      .navbar-collapse {
          border-top: 1px solid transparent;
          box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
      }
      .navbar-fixed-top {
          top: 0;
          border-width: 0 0 1px;
      }
      .navbar-collapse.collapse {
          display: none!important;
      }
      .navbar-nav {
          float: none!important;
          margin-top: 7.5px;
      }
      .navbar-nav>li {
          float: none;
      }
      .navbar-nav>li>a {
          padding-top: 10px;
          padding-bottom: 10px;
      }
      .collapse.in{
            display:block !important;
    overflow:scroll !important;
      }
  }
  /* media query ends......................................................................... */
  
  