body{
    background-color: #e0e0dd;
}



.collection{
    margin-top: 18%;
}

.images{
    width: 100%;
    object-fit: cover;
}

.row{
    margin-left: 1%;
    margin-right: 2%;
}

.itemname{
     font-family: Teko; 
    letter-spacing: 0.1em;
    color:black;
    display: inline;
    font-size: 18px;
    margin-left: 2%;
    font-weight: bold;
}

.price{
    float: right;
    margin-right: 4%;
}

#data{
    opacity: 0;
    transition: all 0.5s;
}
#piece:hover #data{
    opacity: 1;
}

.loader{
    margin-left: 25%;
    align-items: center;
}

@media only screen and (min-width : 468px) and (max-width : 1024px){
    #piece .row img{
        width: 96vw;
        position: relative;
        padding: 2px;
        object-fit: cover;
    }
       
    .price{
      float: none !important;
      display: grid;
    }

    #data{
        opacity: 1;
        transition: all 0.5s;
    }
    #piece:hover #data{
        opacity: 1;
    }
       
}

