*{
    margin: 0;
    padding:0;
    
}

.bag{
    margin-top: 2%;
}

.heading h1{
    margin-top: 5%;
    margin-left: 25%;
    font-family: 'Zilla Slab', serif;
    font-size: 30px;
    font-weight: bold;
}

.cart{
    width: 900px;
    height: 320px;
    border:1px solid gray;
    align-items: center;
    justify-content: center;
    margin-left: 25%;

}

.image{
    height: 270px;
    width: 300px;
    object-fit: contain;
    float: left;
    margin-top: 5%;
}

.cart-name{
    margin-left: 25%;
    margin-top: 10%;
}

.brand{
    font-family: 'Zilla Slab', serif;
    font-size: 25px;
    color: Black;
    font-weight: bold;
}

.name{
    font-family: Teko;
    font-size: 22px;
    color: black;
    letter-spacing: 0.1em;

}

.size{
    font-family: Teko;
    font-size: 20px;
    color: black;
    font-weight: bold;
    letter-spacing: 0.1em;
}
    

.qty{
    font-family: Teko;
    font-size: 20px;
    color: black;
}

.cost{
    font-family:Teko;
    font-size: 20px;
    letter-spacing: 0.1em;
    font-weight: bold;
    color: black;
}

.removeitem{
    align-items: center;
    margin-left: 25% ;
    margin-top: 5%;
}

.removeitem button{
  border: 4px solid #5d4954; 
  color: black;
  background-color: transparent;
  padding: 12px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  font-family: Teko;
  letter-spacing: 0.1em;

}

.removeitem button:hover{
    background-color:#f0f0f0;
    font-size: 19px;
}

.bag p{
    font-family: 'Zilla Slab', serif;
    margin-top: 2%;
    margin-left: 55%;
    font-size: 25px;
    font-weight: bold;
}

.bag .placeorder{
  background-color: #5d4954; 
  color: black;
  padding: 12px 60px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  font-family: Teko;
  letter-spacing: 0.1em;
  margin-top: 2%;
  margin-left: 55%;
  margin-bottom: 5%;
}

.bag .placeorder:hover{
    border:2px solid black;
    font-size: 19px;
}

.emptycart h1{
    margin-top: 15%;
    margin-bottom: 50%;
    font-family: 'Zilla Slab', serif;
}

@media only screen and (min-width : 468px) and (max-width : 1524px){
    .cartdisplay{
        margin-left: -28%;
        margin-top: 6%;
    }
    .heading h1{
        margin-top: 12%;
        margin-left: 4%;
    }
    .emptycart h1{
        margin-bottom: 160%;
    }
}