/* font-family: 'Dancing Script', cursive;
font-family: 'Teko', sans-serif;
font-family: 'Zilla Slab', serif; */


*{
    margin: 0;
    padding:0;
}

body{
    background-color: white;
}



.responsive1{
    object-fit: contain;
    margin-left: -2%;
    width: 100%;
    height: auto; 
    margin-top: 2%; 
    padding-top: 4%; 
    object-position: center top;
    
}

.fixed{
    display: flex;
    position: fixed; 
    align-items: center;
    flex-direction: column;
    top:0; 
    height: calc(100vh - 0.639vw);  
    background: white; 
    margin-top: 10%;
    margin-left: 15%;
}



.header{
    color: #37383d;
    font-size: 45px;
    font-family: 'Zilla Slab', serif;
    letter-spacing: 0.1em;
    font-weight: 600;
    margin-left: -12%;
}

.subheader{
    color: #97989e;
    font-size:24px;
    margin-top: 1%;
    font-family: Teko;
    letter-spacing: 0.1em;
    margin-left: -12%;
    
}

.prices{
     font-size:30px;
    font-family: Teko;
    float: left;
    margin-left: -18%;

}

.size-box{
    margin-left: -3%;
}

.size-box p{
    margin-top:40% ;
    margin-bottom: 19%;
   font-size:23px ;
    font-family:Teko;
    color: #97989e;
    letter-spacing: 0.1em;
}



.option{
    border: none;
    font-family: Teko;
    padding: 25px;
    font-size:22px;
    background-color: transparent;
    display: inline;
    cursor: pointer;
   

}

.option:hover{
    font-size:25px;
    border:1px solid black;
}



.b2{
  background-color: transparent; 
  border: 1px solid black;
  color: black;
  padding: 20px 110px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 22px; 
  font-family: Teko;
  margin-top: 10%;
 margin-left: 15px;

}

.b2:hover{
    font-size:23px;
    border:2px solid black;
} 


