.info{
    margin-top: 10%;
    font-family: 'Zilla Slab', serif;
    font-size: 20px;
    font-weight: bold;
    margin-left: 15%;
}

.orders{
    width: 900px;
    height: 320px;
    border:1px solid gray;
    align-items: center;
    justify-content: center;
    
}

.picture{
    height: 270px;
    width: 300px;
    object-fit: contain;
    float: left;
    margin-top: 5%;
   
}

.cart-name{
    margin-left: 25%;
    margin-top: 10%;
}

.brand{
    font-family: 'Zilla Slab', serif;
    font-size: 25px;
    color: Black;
    font-weight: bold;
}

.name{
    font-family: Teko;
    font-size: 22px;
    color: black;
    letter-spacing: 0.1em;

}

.size{
    font-family: Teko;
    font-size: 20px;
    color: black;
    font-weight: bold;
    letter-spacing: 0.1em;
}
 

@media only screen and (min-width : 468px) and (max-width : 1524px){
    .orderdisplay{
        margin-left: -32%;
    }
}
